    getLocation(address: any) {
        this.latitude = null;
        this.longitude = null;
        if (address != null) {
            this.http.get("https://maps.google.com/maps/api/geocode/json?address=" + address + "&key=AIzaSyDvC4D3B6FJvtMZLOfUsIUnpUxpET_uc-w").subscribe(
                data => {
                    var dataMap = JSON.stringify(data);
                    var mapParse = JSON.parse(dataMap);
                    var mapList = JSON.parse(mapParse["_body"]);
                    if (mapList != null) {
                        this.latitude = mapList["results"][0]["geometry"]["location"]["lat"];
                        this.longitude = mapList["results"][0]["geometry"]["location"]["lng"];
                        this.zoom = 10;
                        var datalist = {
                            "latitude": this.latitude,
                            "longitude": this.longitude
                        }
                        this.latlongList.push(datalist);
                    }
                }
            );
        }
    }

    getJobDateType() {
        this.jobPlaceHolder = 'Date';
        if (this.dashJPage.controls['dateType'].value == 2) {
            this.isWeekSelectForJob = true;
        } else {
            this.isWeekSelectForJob = false;
        }
    }

    getMaintenanceDateType() {
        this.maintenancePlaceHolder = 'Date';
        if (this.dashMPage.controls['mtType'].value == 2) {
            this.isWeekSelectForMaintenance = true;
        } else {
            this.isWeekSelectForMaintenance = false;
        }

        if (this.dashMPage.controls['mtType'].value == 4) {
            this.startViewMain = 'year';
        } else {
            this.startViewMain = 'month';
        }
    }

    //Get maintenance list with filter for plot on map
    getMaintenanceMapForDashboard() {
        $('.loader').show();
        let FromDate = null;
        let ToDate = null;
        let IsWeek = null;
        let IsMonth = null;
        let IsYear = null;
        if (this.dashMPage.controls["mapDate"].value != null) {
            if (this.dashMPage.controls['mtType'].value == 2) {
                this.isWeekSelectForMaintenance = true;
                IsWeek = true;
                //Get fisrt and last day of week from selected date 
                const curr = this.dashMPage.controls["mapDate"].value;
                const first = curr.getDate() - curr.getDay(); // First day is the day of the month - the day of the week
                const last = first + 6; // last day is the first day + 6

                const firstday = new Date(curr.setDate(first)).toUTCString();
                const lastday = new Date(curr.setDate(last)).toUTCString();

                FromDate = this.datepipe.transform(firstday, 'yyyy/MM/dd HH:mm:ss');

                ToDate = this.datepipe.transform(lastday, 'yyyy/MM/dd HH:mm:ss');

                this.maintenanceWeekDate = this.datepipe.transform(firstday, 'dd/MM/yyyy') + ' - ' + this.datepipe.transform(lastday, 'dd/MM/yyyy');
                this.dashMPage.controls['mapDate'].setValue('');
                this.maintenancePlaceHolder = '';
            } else {
                if (this.dashMPage.controls['mtType'].value == 3) {
                    IsMonth = true;
                } else if (this.dashMPage.controls['mtType'].value == 4) {
                    IsYear = true;
                }
                this.maintenanceWeekDate = '';
                FromDate = this.datepipe.transform(this.dashMPage.controls["mapDate"].value, 'yyyy/MM/dd HH:mm:ss');
            }
        }
        var contact = {
            UserID: this.userProfile.ID,
            Status: this.dashMPage.controls["mtStatus"].value,
            FromDate: FromDate,
            ToDate: ToDate,
            IsWeek: IsWeek,
            IsMonth: IsMonth,
            IsYear: IsYear
        };
        this.workOrderService.getMaintenanceMapForDashboard(this.commonService.baseApiUrl, contact).subscribe(
            data => {
                var dataUsers = JSON.stringify(data);
                var userParse = JSON.parse(dataUsers);
                var userInfo = JSON.parse(userParse["_body"]);
                this.maintenanceList = null;
                this.latlongmaintenanceList = [];
                if (userInfo != null) {
                    console.log(userInfo);
                    this.maintenanceList = userInfo;
                    $('.loader').hide();
                    for (let mapData of this.maintenanceList) {
                        this.getMaintennaceLocation(mapData.AddressLine);
                    }
                }
            });
    }

    getMaintennaceLocation(address: any) {
        this.latitude = null;
        this.longitude = null;
        if (address != null) {
            this.http.get("https://maps.google.com/maps/api/geocode/json?address=" + address + "&key=AIzaSyDvC4D3B6FJvtMZLOfUsIUnpUxpET_uc-w").subscribe(
                data => {
                    var dataMap = JSON.stringify(data);
                    var mapParse = JSON.parse(dataMap);
                    var mapList = JSON.parse(mapParse["_body"]);
                    if (mapList != null) {
                        this.latitude = mapList["results"][0]["geometry"]["location"]["lat"];
                        this.longitude = mapList["results"][0]["geometry"]["location"]["lng"];
                        this.zoom = 10;
                        var datalist = {
                            "latitude": this.latitude,
                            "longitude": this.longitude
                        }
                        this.latlongmaintenanceList.push(datalist);
                    }
                }
            );
        }
    }

    //Draw line chart for orders/maintenance status
    initLineChart() {
        const dataStraightLinesChart1 = {
            labels: ['M', 'T', 'W', 'T', 'F', 'S', 'S'],
            series: [
                [10, 16, 8, 13, 20, 15, 20, 34, 30],
                [8, 12, 19, 23, 10, 25, 10, 24, 20]
            ]
        };

        const dataStraightLinesChart2 = {
            labels: ['\'7', '\'8', '\'9', '\'10', '\'11', '\'12', '\'13', '\'14', '\'15'],
            series: [
                [10, 16, 8, 13, 20, 15, 20, 34, 30]
            ]
        };

        const dataStraightLinesChart3 = {
            labels: ['12p', '3p', '6p', '9p', '12p', '3a', '6a', '9a'],
            series: [
                [10, 16, 8, 13, 20, 15, 20, 34, 30]
            ]
        };

        const optionsStraightLinesChart: any = {
            lineSmooth: Chartist.Interpolation.cardinal({
                tension: 0
            }),
            axisY: {
                showGrid: true,
                offset: 40
            },
            axisX: {
                showGrid: false,
            },
            low: 0,
            high: 40,
            showPoint: true,
            chartPadding: { top: 0, right: 0, bottom: 0, left: 0 },
            height: '200px'
        };

        const straightLinesChart1 = new Chartist.Line('#straightLinesChart1', dataStraightLinesChart1,
            optionsStraightLinesChart);

        const straightLinesChart2 = new Chartist.Line('#straightLinesChart2', dataStraightLinesChart2,
            optionsStraightLinesChart);

        const straightLinesChart3 = new Chartist.Line('#straightLinesChart3', dataStraightLinesChart3,
            optionsStraightLinesChart);

        const straightLinesChart4 = new Chartist.Line('#straightLinesChart4', dataStraightLinesChart1,
            optionsStraightLinesChart);
    }

    //Draw pie chart for orders/maintenance status
    initPieChart() {
        const dataPreferences = {
            labels: ['62%', '32%', '6%'],
            series: [62, 32, 6]
        };

        const optionsPreferences = {
            height: '230px'
        };

        new Chartist.Pie('#chartPreferences2', dataPreferences, optionsPreferences);
        new Chartist.Pie('#chartPreferences3', dataPreferences, optionsPreferences);
        new Chartist.Pie('#chartPreferences4', dataPreferences, optionsPreferences);
    }

